import React, { PureComponent } from 'react'
import { message, Button } from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
@connect(
  (state) => ({
    ...state.materialRequestSummary
  }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    let objValues = Object.values(reqObj)
    delete reqObj.initTime
    delete reqObj.initDate
    let flag = false
    objValues &&
      objValues.length > 0 &&
      objValues.map((item, index, arr) => {
        if (item) {
          flag = true
        }
        return flag
      })
    if (flag) {
      handleExport({ searchParams: { page, size, ...reqObj } }, () => {
        message.success('导出成功')
      })
    } else {
      message.error('未选择、输入部分任何筛选条件')
    }
  }
  render() {
    const { modalTuttonLoading, menuPermissionIds } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds &&
          menuPermissionIds.includes('materialRequestSummary_export') && (
            <Button
              type="primary"
              className={style.btn}
              onClick={this.handleExport}
              loading={modalTuttonLoading}
            >
              导出
            </Button>
          )}
      </div>
    )
  }
}



