import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, DatePicker } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect, CsxMultipleSelect } from 'Form'
import {
  API_GET_MRP_CODEANDNAME,
  API_GET_PRODUCTION_CODEANDNAME,
  API_GET_LOCATION_CODEANDNAME
} from 'api'
const FormItem = Form.Item
const { RangePicker } = DatePicker
import moment from 'moment'
@connect(
  state => ({
    ...state.materialRequestSummary
  }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = e => {
    const { handleGetList, searchParams, size, form } = this.props
    console.log('e is', e)
    // return 111
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        console.log('form values is', values)
        let reqObj = { ...searchParams }
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            reqObj[i] = reqObj[i].value
          }
        }
        delete reqObj.initTime
        delete reqObj.initDate
        handleGetList({ searchParams: { ...reqObj, page: 1, size } })
      }
    })
  }

  // 重置
  reset = () => {
    const { update, searchParams } = this.props
    const { initDate, produceTimeStart, produceTimeEnd } = searchParams
    console.log('initDate is', moment(initDate).unix())
    update({
      searchParams: {
        initDate,
        produceTimeStart,
        produceTimeEnd
      }
    })
  }
  render() {
    const {
      searchParams = {},
      menuPermissionIds = [],
      form: { getFieldDecorator }
    } = this.props
    const {
      locationCode,
      productSearchKey,
      mrpPropKey,
      initTime,
      initDate
    } = searchParams
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    return (
      <Form className={style.form} onSubmit={this.query}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <CsxSelect
              allowClear={true}
              dateApi={API_GET_LOCATION_CODEANDNAME}
              label='地点编码/名称'
              allowClear={true}
              selectItem={{
                data: locationCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                console.log('value is', value)
                this.onInputChange({ locationCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              allowClear={true}
              dateApi={API_GET_PRODUCTION_CODEANDNAME}
              label='物料名称/编码'
              selectItem={{
                data: productSearchKey,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ productSearchKey: value })
              }}
            />
          </Col>

          <Col span={8}>
            <CsxMultipleSelect
              allowClear={true}
              dateApi={API_GET_MRP_CODEANDNAME}
              label='评估类'
              selectItem={{
                data: mrpPropKey,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={data => {
                this.onInputChange({ mrpPropKey: data })
              }}
            />
          </Col>

          <Col span={8}>
            <div className={'commonInput'}>
              {/* <label>生产日期：</label> */}
              <div
                className='boxInSearch'
                style={{ marginRight: 0, width: '100%' }}
              >
                <FormItem {...formItemLayout} label='生产日期'>
                  {getFieldDecorator('initDate', {
                    initialValue: initDate,
                    rules: [
                      {
                        required: true,
                        message: '不能为空!'
                      }
                    ]
                  })(
                    <DatePicker
                      allowClear={false}
                      // showTime
                      // value={initDate}
                      onChange={time => {
                        let produceTimeStart =
                          time && `${moment(time).format('YYYY-MM-DD')}`
                        let produceTimeEnd = produceTimeStart
                        this.onInputChange({
                          produceTimeStart,
                          produceTimeEnd,
                          initDate: time
                        })
                      }}
                      format='YYYY-MM-DD'
                    />
                  )}
                </FormItem>
              </div>
            </div>
          </Col>
          <Col span={8}>
            <div className={'commonInput showTimePicker'}>
              <label>创建日期：</label>
              <div className='box' style={{ marginRight: 0 }}>
                <RangePicker
                  allowClear={true}
                  showTime
                  value={initTime}
                  onChange={time => {
                    let createTimeStart =
                      time &&
                      time[0] &&
                      `${moment(time[0]).format('YYYY-MM-DD HH:mm:ss')}`
                    let createTimeEnd =
                      time &&
                      time[1] &&
                      `${moment(time[1]).format('YYYY-MM-DD HH:mm:ss')}`
                    this.onInputChange({
                      createTimeStart,
                      createTimeEnd,
                      initTime: time
                    })
                  }}
                  format='YYYY-MM-DD HH:mm:ss'
                />
              </div>
            </div>
          </Col>

          <Col span={24}>
            <Row type='flex' justify='end'>
              {menuPermissionIds &&
                menuPermissionIds.includes('materialRequestSummary_find') && (
                  <Button
                    type='primary'
                    className={style.mr10}
                    onClick={this.query}
                  >
                    查询
                  </Button>
                )}
                <Button type='primary' onClick={this.reset}>
                    重置
                  </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



