import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.materialRequestSummary,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({ searchResult: [], searchParams: {} })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        reqObj[i] = reqObj[i].value
      }
    }
    delete reqObj.initTime
    delete reqObj.initDate
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }
  render() {
    const {
      history,
      menuPermissionIds
     } = this.props
    return (
      <section>
        <Bread route={['生产中心', '物料需求汇总']} />
        <Form menuPermissionIds={menuPermissionIds} />
        <Tool history={history} menuPermissionIds={menuPermissionIds}/>
        <Table history={history} menuPermissionIds={menuPermissionIds}/>
      </section>
    )
  }
}



