import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_MATERIALREQUESTSUMMARYS_LIST,
  API_MATERIALREQUESTSUMMARYS_EXPORT,
  API_MATERIALREQUESTSUMMARYS_DET_SOURCE
} from 'api'
//同步
export const update = object => {
  console.log('object is ', object)
  return {
    type: actionType.MATERIALREQUESTSUMMARY_UPDATE,
    data: object
  }
}

//异步
// 获取列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.MATERIALREQUESTSUMMARY_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_MATERIALREQUESTSUMMARYS_LIST, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page, size } = reqData.searchParams
      dispatch({
        type: actionType.MATERIALREQUESTSUMMARY_UPDATE,
        data: {
          page,
          size,
          tableLoading: false,
          total: data.result.total,
          searchResult: data.result || []
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.MATERIALREQUESTSUMMARY_FAIL
      })
    })
}
// 获取需求汇总详情
export const getMaterialSummaryRequirementDet = (
  reqData,
  callBack
) => dispatch => {
  dispatch({
    type: actionType.MATERIALREQUESTSUMMARY_UPDATE,
    data: { tableLoading: true }
  })
  getData({ api: API_MATERIALREQUESTSUMMARYS_DET_SOURCE, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      callBack && callBack(data)
      dispatch({
        type: actionType.MATERIALREQUESTSUMMARY_UPDATE,
        data: {
          tableLoading: false
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.MATERIALREQUESTSUMMARY_FAIL
      })
    })
}

// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.MATERIALREQUESTSUMMARY_UPDATE,
    data: { modalTuttonLoading: true }
  })
  getData({ api: API_MATERIALREQUESTSUMMARYS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.MATERIALREQUESTSUMMARY_UPDATE,
        data: { modalTuttonLoading: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.MATERIALREQUESTSUMMARY_FAIL
      })
    })
}



