import React, { PureComponent } from 'react'
import { Table, Modal, message } from 'antd'
const confirm = Modal.confirm
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import style from './style/Table.scss'
import moment from 'moment'
@connect(
  state => ({
    ...state.materialRequestSummary,
    userLoation: state.userLoation
  }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      materrialReqList: []
    }
    this.columns = [
      {
        title: '地点编码',
        width: 100,
        dataIndex: 'locationCode',
        key: 'locationCode'
      },
      {
        title: '评估类',
        width: 150,
        dataIndex: 'mrpValue',
        key: 'mrpValue',
        render: (text, record) => {
          const { mrpKey, mrpValue } = record
          return `${mrpKey} ${mrpValue}`
        }
      },
      {
        title: '物料编码',
        width: 100,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '物料名称',
        width: 150,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '规格',
        width: 80,
        dataIndex: 'spec',
        key: 'spec'
      },
      {
        title: '单位',
        width: 80,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '总需求量',
        width: 80,
        dataIndex: 'totalQty',
        key: 'totalQty',
        render: text => (
          <div>
            {/* <Tooltip placement="bottomLeft" title={planAndActualData} trigger='click'
              onVisibleChange = { this.onVisibleChange.bind(this, record) }
              overlayClassName={'tootip_in_table'} >
                <span className={style.btn_ib_table}>{text}</span>
            </Tooltip> */}
            {Number(text).toFixed(2)}
          </div>
        )
      },
      {
        title: '当前库存量',
        width: 100,
        dataIndex: 'currentQty',
        key: 'currentQty'
      },
      {
        title: '物料缺口量',
        width: 100,
        dataIndex: 'needQty',
        key: 'needQty',
        render: text => <div>{Number(text).toFixed(2)}</div>
      },
      {
        title: '生产日期',
        width: 150,
        dataIndex: 'produceTime',
        key: 'produceTime'
      }
    ]
  }
  // 查看明细
  onVisibleChange = (record, bool) => {
    const { getMaterialSummaryRequirementDet } = this.props
    const { mrOrderCode = '', productCode = '' } = record
    if (bool) {
      getMaterialSummaryRequirementDet(
        {
          searchParams: { mrOrderCode, productCode }
        },
        data => {
          const { result = [] } = data
          const formatterData = [...result]
          this.setState({
            materrialReqList: formatterData
          })
        }
      )
    }
  }
  handleOperator(record) {
    const {
      handleGetList,
      searchParams,
      page,
      size,
      handleOperator
    } = this.props
    confirm({
      title: '温馨提示',
      content: '是否删除该储位？',
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleOperator(
          {
            data: {
              id: record.id
            },
            urlParams: [record.id]
          },
          () => {
            message.success('操作成功')
            handleGetList({ searchParams: { page, size, ...searchParams } })
          }
        )
      }
    })
  }
  linkToDet = record => {
    const { history } = this.props
    return history.push(
      `/mms/settingCenter/materialRequestSummary/det/${record.id}?editOrCreate=edit`
    )
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size, update } = this.props
    update({
      searchParams: {
        initDate: moment(),
        produceTimeStart: moment().format('YYYY-MM-DD'),
        produceTimeEnd: moment().format('YYYY-MM-DD')
      }
    })
    handleGetList({
      searchParams: {
        page,
        size,
        ...searchParams,
        produceTimeStart: moment().format('YYYY-MM-DD'),
        produceTimeEnd: moment().format('YYYY-MM-DD')
      }
    })
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      delete reqObj.initTime
      delete reqObj.initDate
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  // 需求数据明细
  renderResourceData = () => {
    const { materrialReqList = [] } = this.state
    return (
      <div className={style.tr_in_table}>
        <tr>
          <th className={style.td_in_table_one}>生产波次</th>
          <th className={style.td_in_table_one}>生产工单</th>
          <th className={style.td_in_table_one}>商品编码</th>
          <th className={style.td_in_table_one}>商品名称</th>
          <th className={style.td_in_table_one}>需求量</th>
        </tr>
        {materrialReqList &&
          materrialReqList.length > 0 &&
          materrialReqList.map(item => {
            const {
              batchNumber = '',
              taskOrderCode = '',
              produceCode = '',
              produceName = '',
              requireQty = ''
            } = item
            return (
              <tr>
                <td>{batchNumber}</td>
                <td>{taskOrderCode}</td>
                <td>{produceCode}</td>
                <td>{produceName}</td>
                <td>{requireQty}</td>
              </tr>
            )
          })}
      </div>
    )
  }
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    handleExport({ searchParams: { page, size, ...searchParams } }, () => {
      message.success('导出成功')
    })
  }
  render() {
    const { tableLoading, selectedRowKeys, searchResult = [] } = this.props
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.productCode}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={false}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



